"use client";

import { useState, useEffect } from "react";
import { 
  Users, Trash2, Search, User, Store, AlertTriangle, 
  CheckCircle, Filter, Download, BarChart3, Bell, Settings,
  ChevronDown, X
} from "lucide-react";
import { Montserrat } from "next/font/google";

// Configuration de la police Montserrat pour cohérence avec les autres pages
const montserrat = Montserrat({
  subsets: ["latin"],
  weight: ["500", "600", "700", "800"],
  display: "swap",
});

// ... (Interfaces restent identiques, sauf qu'on supprime verificationStatus de Seller)
interface Address {
  street: string;
  province: string;
  postalCode: string;
}

interface BaseUser {
  id: number;
  firstName: string;
  lastName: string;
  email: string;
  createdAt: string;
  phone?: string;
  address?: Address;
  image?: string;
  status: "active" | "suspended" | "pending";
}

interface Client extends BaseUser {
  ordersCount: number;
  totalSpent: number;
}

interface Seller extends BaseUser {
  shopName: string;
  reportsCount: number;
  productsCount: number;
  rating: number;
}

// ... (Le début du composant reste identique jusqu'au tableau)

export default function UsersPage() {
  // ... (Tous les useState et fonctions jusqu'à return restent identiques)

  return (
    <div className={`min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 py-8 px-4 sm:px-6 lg:px-8 ${montserrat.className}`}>
      <div className="max-w-7xl mx-auto">
        {/* ... (Notifications, En-tête, Statistiques rapides restent identiques) */}

        {/* Section Tableau des utilisateurs */}
        <div className="bg-white rounded-xl shadow border border-gray-200 overflow-hidden">
          <div className="p-4 border-b border-gray-200">
            {/* ... (Filtres et barre de recherche restent identiques) */}
          </div>
          
          {/* Tableau */}
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Utilisateur
                  </th>
                  <th 
                    className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer hover:bg-gray-100"
                    onClick={() => handleSort('createdAt')}
                  >
                    <div className="flex items-center gap-1">
                      Date d'inscription
                      {sortConfig?.key === 'createdAt' && (
                        <ChevronDown className={`h-4 w-4 ${sortConfig.direction === 'desc' ? 'transform rotate-180' : ''}`} />
                      )}
                    </div>
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Statut
                  </th>
                  {userType === "client" ? (
                    <>
                      <th 
                        className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer hover:bg-gray-100"
                        onClick={() => handleSort('ordersCount')}
                      >
                        <div className="flex items-center gap-1">
                          Commandes
                          {sortConfig?.key === 'ordersCount' && (
                            <ChevronDown className={`h-4 w-4 ${sortConfig.direction === 'desc' ? 'transform rotate-180' : ''}`} />
                          )}
                        </div>
                      </th>
                      <th 
                        className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer hover:bg-gray-100"
                        onClick={() => handleSort('totalSpent')}
                      >
                        <div className="flex items-center gap-1">
                          Total dépensé
                          {sortConfig?.key === 'totalSpent' && (
                            <ChevronDown className={`h-4 w-4 ${sortConfig.direction === 'desc' ? 'transform rotate-180' : ''}`} />
                          )}
                        </div>
                      </th>
                    </>
                  ) : (
                    <>
                      <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                        Boutique
                      </th>
                      <th 
                        className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer hover:bg-gray-100"
                        onClick={() => handleSort('rating')}
                      >
                        <div className="flex items-center gap-1">
                          Note
                          {sortConfig?.key === 'rating' && (
                            <ChevronDown className={`h-4 w-4 ${sortConfig.direction === 'desc' ? 'transform rotate-180' : ''}`} />
                          )}
                        </div>
                      </th>
                    </>
                  )}
                  <th className="px-4 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-100">
                {filteredUsers.length > 0 ? (
                  filteredUsers.map((user) => (
                    <tr
                      key={user.id}
                      className="hover:bg-gray-50 transition-colors cursor-pointer"
                      onClick={() => handleUserClick(user)}
                    >
                      <td className="px-4 py-3 whitespace-nowrap">
                        <div className="flex items-center gap-3">
                          {user.image ? (
                            <img
                              src={user.image}
                              alt={`${user.firstName} ${user.lastName}`}
                              className="w-10 h-10 rounded-full object-cover border border-gray-200"
                            />
                          ) : (
                            <div
                              className={`w-10 h-10 rounded-full flex items-center justify-center text-white text-sm font-bold ${
                                getInitialsAndColor(user.firstName, user.lastName, user.id).color
                              }`}
                            >
                              {getInitialsAndColor(user.firstName, user.lastName, user.id).initials}
                            </div>
                          )}
                          <div>
                            <div className="font-medium text-gray-900">
                              {user.firstName} {user.lastName}
                            </div>
                            <div className="text-xs text-gray-500">{user.email}</div>
                          </div>
                        </div>
                      </td>
                      <td className="px-4 py-3 whitespace-nowrap">
                        <div className="text-sm text-gray-600">{user.createdAt}</div>
                      </td>
                      <td className="px-4 py-3 whitespace-nowrap">
                        <span
                          className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium border ${getStatusClass(
                            user.status
                          )}`}
                        >
                          {user.status === "active" && "Actif"}
                          {user.status === "suspended" && "Suspendu"}
                          {user.status === "pending" && "En attente"}
                        </span>
                      </td>
                      {userType === "client" ? (
                        <>
                          <td className="px-4 py-3 whitespace-nowrap">
                            <div className="text-sm font-medium text-gray-900">
                              {(user as Client).ordersCount}
                            </div>
                          </td>
                          <td className="px-4 py-3 whitespace-nowrap">
                            <div className="text-sm font-medium text-gray-900">
                              {(user as Client).totalSpent.toFixed(2)} TND
                            </div>
                          </td>
                        </>
                      ) : (
                        <>
                          <td className="px-4 py-3 whitespace-nowrap">
                            <div className="flex items-center gap-2">
                              <Store className="h-4 w-4 text-indigo-600" />
                              <span className="text-sm font-medium text-gray-800">
                                {(user as Seller).shopName}
                              </span>
                            </div>
                          </td>
                          <td className="px-4 py-3 whitespace-nowrap">
                            <div className="text-sm font-medium text-gray-900">
                              {(user as Seller).rating.toFixed(1)}/5
                            </div>
                          </td>
                        </>
                      )}
                      <td className="px-4 py-3 whitespace-nowrap text-right">
                        <div className="flex items-center justify-end gap-2">
                          <button
                            onClick={(e) => {
                              e.stopPropagation();
                              setUserToAction(user);
                              setShowActionModal(true);
                            }}
                            className="p-1 text-gray-600 hover:text-indigo-600 transition"
                            title="Gérer l'utilisateur"
                          >
                            <Settings className="h-4 w-4" />
                          </button>
                          <button
                            onClick={(e) => {
                              e.stopPropagation();
                              handleDeleteUser(user.id, userType);
                            }}
                            className="p-1 text-gray-600 hover:text-red-600 transition"
                            title="Supprimer"
                          >
                            <Trash2 className="h-4 w-4" />
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td colSpan={userType === "client" ? 7 : 7} className="px-4 py-8 text-center">
                      <div className="text-gray-500 text-sm">
                        Aucun {userType === "client" ? "client" : "vendeur"} trouvé
                      </div>
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>

        {/* Modal de confirmation de suppression */}
        {showDeleteModal && (
          <div className="fixed inset-0 bg-gray-600 bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-white rounded-lg p-6 max-w-md w-full">
              <h3 className="text-lg font-semibold text-gray-900 mb-2">
                Confirmer la suppression
              </h3>
              <p className="text-gray-600 mb-6">
                Êtes-vous sûr de vouloir supprimer cet utilisateur ? Cette action est irréversible.
              </p>
              <div className="flex justify-end gap-3">
                <button
                  onClick={() => setShowDeleteModal(false)}
                  className="px-4 py-2 text-gray-600 bg-gray-100 rounded-lg hover:bg-gray-200 transition"
                >
                  Annuler
                </button>
                <button
                  onClick={confirmDelete}
                  className="px-4 py-2 text-white bg-red-600 rounded-lg hover:bg-red-700 transition"
                >
                  Supprimer
                </button>
              </div>
            </div>
          </div>
        )}

        {/* Modal d'actions utilisateur */}
        {showActionModal && userToAction && (
          <div className="fixed inset-0 bg-gray-600 bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-white rounded-lg p-6 max-w-md w-full">
              <div className="flex justify-between items-center mb-4">
                <h3 className="text-lg font-semibold text-gray-900">
                  Gérer {userToAction.firstName} {userToAction.lastName}
                </h3>
                <button
                  onClick={() => setShowActionModal(false)}
                  className="text-gray-500 hover:text-gray-700"
                >
                  <X className="h-5 w-5" />
                </button>
              </div>
              
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Statut
                  </label>
                  <select
                    value={userToAction.status}
                    onChange={(e) => handleUserStatusChange(userToAction.id, e.target.value as any)}
                    className="w-full px-3 py-2 border border-gray-200 rounded-md focus:ring-indigo-500 focus:border-indigo-500"
                  >
                    <option value="active">Actif</option>
                    <option value="suspended">Suspendu</option>
                    <option value="pending">En attente</option>
                  </select>
                </div>

                {userType === "seller" && (
                  <button
                    onClick={() => handleWarnSeller(userToAction.id)}
                    className="w-full flex items-center justify-center gap-2 px-4 py-2 bg-amber-100 text-amber-700 rounded-lg hover:bg-amber-200 transition"
                  >
                    <Bell className="h-4 w-4" />
                    Envoyer un avertissement
                  </button>
                )}
              </div>
            </div>
          </div>
        )}

        {/* Carte statistique au lieu du modal de détails */}
        {selectedUser && (
          <div className="fixed inset-0 bg-gray-600 bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-white rounded-xl shadow-lg p-6 max-w-lg w-full">
              <div className="flex justify-between items-center mb-6">
                <h3 className="text-xl font-bold text-gray-900">
                  Statistiques de {selectedUser.firstName} {selectedUser.lastName}
                </h3>
                <button
                  onClick={() => setSelectedUser(null)}
                  className="text-gray-500 hover:text-gray-700"
                >
                  <X className="h-5 w-5" />
                </button>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="bg-white rounded-xl shadow-sm p-4 border border-gray-100">
                  <div className="flex justify-between">
                    <div>
                      <p className="text-gray-500 text-sm font-medium">Statut</p>
                      <h3 className={`text-lg font-bold mt-1 ${getStatusClass(selectedUser.status).split(' ')[1]}`}>
                        {selectedUser.status === "active" ? "Actif" : 
                         selectedUser.status === "suspended" ? "Suspendu" : "En attente"}
                      </h3>
                    </div>
                    <div className="bg-indigo-100 p-2 rounded-lg">
                      <User className="h-6 w-6 text-indigo-600" />
                    </div>
                  </div>
                </div>

                <div className="bg-white rounded-xl shadow-sm p-4 border border-gray-100">
                  <div className="flex justify-between">
                    <div>
                      <p className="text-gray-500 text-sm font-medium">Inscription</p>
                      <h3 className="text-lg font-bold mt-1">{selectedUser.createdAt}</h3>
                    </div>
                    <div className="bg-blue-100 p-2 rounded-lg">
                      <Users className="h-6 w-6 text-blue-600" />
                    </div>
                  </div>
                </div>

                {userType === "client" ? (
                  <>
                    <div className="bg-white rounded-xl shadow-sm p-4 border border-gray-100">
                      <div className="flex justify-between">
                        <div>
                          <p className="text-gray-500 text-sm font-medium">Commandes</p>
                          <h3 className="text-lg font-bold mt-1">{(selectedUser as Client).ordersCount}</h3>
                        </div>
                        <div className="bg-emerald-100 p-2 rounded-lg">
                          <CheckCircle className="h-6 w-6 text-emerald-600" />
                        </div>
                      </div>
                    </div>

                    <div className="bg-white rounded-xl shadow-sm p-4 border border-gray-100">
                      <div className="flex justify-between">
                        <div>
                          <p className="text-gray-500 text-sm font-medium">Total dépensé</p>
                          <h3 className="text-lg font-bold mt-1">{(selectedUser as Client).totalSpent.toFixed(2)} TND</h3>
                        </div>
                        <div className="bg-amber-100 p-2 rounded-lg">
                          <BarChart3 className="h-6 w-6 text-amber-600" />
                        </div>
                      </div>
                    </div>
                  </>
                ) : (
                  <>
                    <div className="bg-white rounded-xl shadow-sm p-4 border border-gray-100">
                      <div className="flex justify-between">
                        <div>
                          <p className="text-gray-500 text-sm font-medium">Produits</p>
                          <h3 className="text-lg font-bold mt-1">{(selectedUser as Seller).productsCount}</h3>
                        </div>
                        <div className="bg-purple-100 p-2 rounded-lg">
                          <Store className="h-6 w-6 text-purple-600" />
                        </div>
                      </div>
                    </div>

                    <div className="bg-white rounded-xl shadow-sm p-4 border border-gray-100">
                      <div className="flex justify-between">
                        <div>
                          <p className="text-gray-500 text-sm font-medium">Note</p>
                          <h3 className="text-lg font-bold mt-1">{(selectedUser as Seller).rating.toFixed(1)}/5</h3>
                        </div>
                        <div className="bg-cyan-100 p-2 rounded-lg">
                          <BarChart3 className="h-6 w-6 text-cyan-600" />
                        </div>
                      </div>
                    </div>

                    <div className="bg-white rounded-xl shadow-sm p-4 border border-gray-100">
                      <div className="flex justify-between">
                        <div>
                          <p className="text-gray-500 text-sm font-medium">Signalements</p>
                          <h3 className="text-lg font-bold mt-1">{(selectedUser as Seller).reportsCount}</h3>
                        </div>
                        <div className="bg-red-100 p-2 rounded-lg">
                          <AlertTriangle className="h-6 w-6 text-red-600" />
                        </div>
                      </div>
                    </div>
                  </>
                )}
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}